.header h1 { padding-bottom: 0; line-height: 62px; }

.header h2 { font: 35px/50px HelveticaNeue, Arial, sans-serif; text-transform: uppercase; padding-bottom: 70px; }

.header .buttonbox { text-align: center; width: 306px; margin: 0 auto; /*box-shadow: 50px 41px 58.75px 66.25px rgba(45, 5, 52, 0.93);*/ box-shadow: 20px 41px 58.75px 66.25px rgba(0, 0, 0, 0.53); position: relative; left: -50px; }

.header .buttonbox a.button { background-color: rgba(140, 11, 163, 0.69); width: 100%; height: 56px; border: 1px solid #c1bac0; font: 18px/54px HelveticaNeue, Arial, sans-serif; color: #fbfaf9; animation: pulse-white-light 1.8s infinite; }

.header .buttonbox a.button:hover { background-color: #8c0ba3; }

.header__bottom { position: absolute; bottom: 8%; left: 0; text-align: center; width: 100%; }

.header__bottom ul { margin: 0; padding: 0; }

.header__bottom li { display: inline-block; margin: 0 4%; position: relative; padding-left: 33px; font: 24px/48px HelveticaNeue, Arial, sans-serif; }

.header__bottom li:after { content: ''; display: block; position: absolute; left: 0; top: 50%; transform: translateY(-50%); width: 15px; height: 15px; border-radius: 50%; background-color: #c50ee6; }

.phone { position: relative; padding-left: 86px; }

.phone .sprite { position: absolute; left: 0; top: 50%; transform: translateY(-50%); }

.phone .sprite:hover { animation: rollOut; cursor: pointer; animation: rollOut 1.8s infinite; }

.phone a { display: block; clear: both; text-decoration: none; font: 500 27px/1.1 HelveticaNeue, Arial, sans-serif; }

.videobutton { border: 3px solid rgba(251, 250, 249, 0.431); border-radius: 50%; width: 60px; height: 60px; position: absolute; top: 0; right: 0; }

.videobutton a { height: 100%; display: block; display: flex; align-items: center; justify-content: center; /*animation: pulse-white 1.5s infinite;*/ animation: pulse-white 1.5s infinite; border-radius: 50%; transition: .4s; padding-right: 1px; }

.videobutton a:hover { animation: pulse-white 0.8s infinite; transform: rotate(360deg); }

.hamburger-fixed { position: fixed; width: 1500px; top: 0; }

.hamburger { background-color: #73158b; padding: 1px 0 0; width: 44px; height: 37px; display: flex; justify-content: center; align-items: center; position: absolute; top: 58px; right: 47px; }

.hamburger-box { width: 30px; height: 18px; }

.hamburger:hover { opacity: 1; }

.hamburger-inner, .hamburger-inner:before, .hamburger-inner:after { width: 100%; height: 3px; background-color: #fff; border-radius: 0; }

.hamburger-inner:before { top: -7px; }

.hamburger-inner:after { bottom: -7px; }

.videobox { width: 262px; height: 123px; position: absolute; right: 184px; top: 25%; }

.videobox__inner { height: 100%; width: 100%; position: relative; }

.videobox .sprite-dash { position: absolute; left: 2px; bottom: 0; }

.videobox__text { position: absolute; left: 0; bottom: 8px; font: 300 18px HelveticaNeue, Arial, sans-serif; }

.videobox__text b { font-weight: 400; display: block; }

@media only screen and (max-width: 1199px) { .videobox { top: 17%; right: 45px; }
  .header__bottom li { margin: 0 2%; font-size: 22px; }
  .header__phone { padding-right: 33px; }
  .header h1, .header h2 { text-align: center; width: 100%; }
  .header .buttonbox { left: 0; } }

@media only screen and (max-width: 991px) { .header__logo { padding-left: 0; max-width: 48%; } }

@media only screen and (max-height: 767px) and (max-width: 767px) { .header { background-image: url(../img/mob-header-bg.jpg); } }

@media only screen and (max-width: 767px) { .phone { padding-left: 62px; margin-top: 12px; }
  .phone a { font-size: 22px; }
  .phone .sprite { transform: translateY(-50%) scale(0.8); }
  .header__text { width: 100%; }
  .header h1 { font: 900 50px/54px HelveticaNeue, Arial, sans-serif; }
  .header h2 { font: 32px/40px HelveticaNeue, Arial, sans-serif; padding-bottom: 40px; } }

@media only screen and (max-width: 599px) { .header__phone { position: relative; z-index: 3; }
  .videobox { top: 134px; }
  .header h1 { font: 900 40px/44px HelveticaNeue, Arial, sans-serif; }
  .header h2 { font: 27px/40px HelveticaNeue, Arial, sans-serif; }
  .header__bottom li { width: 375px; text-align: left; }
  .header__text { padding-bottom: 222px; } }

@media only screen and (max-width: 479px) { .header { min-height: 640px; }
  .header__top { display: block; }
  .header__logo { max-width: 70%; }
  .videobox { top: 46px; right: 15px; }
  .videobox__text { display: none; }
  .videobox .sprite-dash { display: none; }
  .videobutton { width: 50px; height: 50px; }
  .phone { text-align: center; width: 290px; margin: 32px auto 0; }
  .header h1 { font-size: 25px; }
  .header h2 { font-size: 16px; line-height: 1.4; padding-bottom: 50px; }
  .header .buttonbox { width: auto; }
  .header .buttonbox a.button { width: auto; max-width: 100%; padding-left: 52px; padding-right: 52px; }
  .header__bottom li { margin: 0 15px; width: 290px; font: 19px/32px HelveticaNeue, Arial, sans-serif; } }

.zoom-anim-dialog h3 { text-align: center; font-size: 24px; padding-bottom: 30px; }

.zoom-anim-dialog button { animation: none; }

.zoom-anim-dialog button:hover { background: none; }

.zoom-anim-dialog .buttonbox input { animation: none; }

.middle-popup { background: #fff; padding: 7px; text-align: left; max-width: 570px; margin: 40px auto; position: relative; border-radius: 4px; }

.middle-popup .form { width: 400px; margin: 0 auto 14px; max-width: 100%; }

.middle-popup .form input { width: 100%; }

.dialog-inner { border: 4px solid #c50ee6; padding: 25px 15px; border-radius: 4px; }

.inputrow { padding-bottom: 15px; }

@media only screen and (max-width: 479px) { .zoom-anim-dialog h3 { font-size: 22px; padding-bottom: 24px; } }

.content { min-width: 320px; text-align: left; width: 100%; order: 2; -webkit-order: 2; flex-grow: 1; -webkit-flex-grow: 1; background: #83f494; }

.footer { background: #ff0; height: 80px; text-align: left; width: 100%; min-width: 320px; order: 3; -webkit-order: 3; }

